import React from "react"

import { Paper } from "@mui/material"
import Container from '@mui/material/Container';
import Markdown from "../component/Markdown";
import MarkNav from 'markdown-navbar';
import 'markdown-navbar/dist/navbar.css';
import Grid from '@mui/material/Unstable_Grid2';
import { styled } from '@mui/material/styles';
import { Link, Outlet } from "react-router-dom";

import Drawer from '@mui/material/Drawer';
import Toolbar from '@mui/material/Toolbar';
import Box from '@mui/material/Box';

import Header from '../component/layout/Header'
import { get_doc, joke } from "../request/api"


const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'left',
    color: theme.palette.text.secondary,
  }));

class Home extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            jokedata: { city: null },
            doc: "",
        }
    }

    async componentDidMount() {
        var $this = this;

        // const data = await joke()
        // const doc = await get_doc()

        var xmlhttp = new XMLHttpRequest();

        xmlhttp.onreadystatechange = function () {
          if (xmlhttp.readyState === 4 && xmlhttp.status === 200) {
            $this.setState({
              doc: xmlhttp.responseText
            })
          }
        }
        // 文件目录在 public/static/test.md 这里不需要写 public 因为打包之后没有此目录。
        xmlhttp.open("GET", "/static/test.md", true); 
        xmlhttp.send();

        // this.setState({
        //     jokedata: data,
        //     //doc: doc
        // })
    }

    render() {
        console.log("Home render()")
        return (
            <>
                <Grid container rowSpacing={1}>
                    {/* <Grid item xs={12}>
                        <Item> <Header /> </Item>
                    </Grid> */}
                
                    <Grid item xs={12}>
                        <Item>
                            <Box> 
                                <h1> {this.state.count} </h1>
                                <button onClick={() => { this.setState({ count: this.state.count + 1 }) }}> 累加 </button>

                                <ul>
                                    <li> <Link to="/"> Home </Link></li>
                                    <li> <Link to="/test"> Test </Link></li>
                                    <li> <Link to="/list"> Link </Link></li>
                                    <li> <Link to="/map"> Map </Link></li>
                                    <li> <Link to="/login"> Login </Link> </li>
                                </ul>
                            </Box>
                        </Item>
                    </Grid>

            </Grid>
            </>
        )
    }
}

export default Home